.about-container {
  width: 100;
  height: auto;
  font-family: 'NEXON Lv2 Gothic';
  word-break: keep-all;
  overflow-x: hidden;
  overflow-y: visible;
}

/* 메인 */
.about-main {
  width: 100%;
  height: 60vh;
  margin: 10vh 0;
  position: relative;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    z-index: 0;
  }
}

.about-main-text {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  padding: 5% 3% 0 8%;

  & h1 {
    margin: 0 0 2rem 0;
    font-family: 'Inter';
    font-size: 3rem;
    font-weight: 700;
    color: white;
  }

  & p {
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 2.2;
  }
}

/* 키워드 */
.about-keyword {
  width: 100%;
  margin: 20vh 0;
  height: 50vh;
  display: flex;
  flex-direction: column;

  & h1 {
    cursor: pointer;
    align-self: center;
    padding: 0 2px;
    font-family: 'Inter';
    font-size: 3rem;
    font-weight: 700;
    color: black;
    box-shadow: inset 0 -6px 0 #ffee58;
    transition: box-shadow 0.2s, color 0.2s;

    &:hover {
      box-shadow: inset 0 -3.5rem 0 #f57c00;
      color: #fff;
    }
  }
}

.about-keyword-slide {
  width: 100%;
  height: 40vh;
  overflow-x: scroll;
  background-color: #3184ff;
  display: flex;
  flex-direction: row;
  align-items: center;
  scroll-behavior: smooth;

