.about-keyword-slide-text {
  min-width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-right: 1px solid black;

  & h4 {
    margin: 0 0 3rem 0;
    font-family: 'Inter';
    font-size: 5rem;
    font-weight: 700;
  }

  & p {
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.5;
    color: white;
  }
}

.slide__1 h4 {
  color: #ff6883;
  text-shadow: 5px 3px 0 #faff00;
}
.slide__2 h4 {
  color: #faff00;
  text-shadow: 5px 3px 0 #ff6883;
}
.slide__3 h4 {
  color: #00ff1a;
  text-shadow: 5px 3px 0 #ff6883;
}

/* 활동, 액티비티 */
.about-activity {
  max-width: 100%;
  margin: 0 5vh;
  & h1 {
    font-family: 'Inter';
    font-size: 3rem;
    font-weight: 700;
    color: black;
  }
}

.about-activity-content {
  max-width: 100%;
  margin: 2% 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.about-activity-left,
.about-activity-right {
  max-width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5vh;

  & .about-activity-img {
    width: 70%;
    aspect-ratio: 2.5/1;
    padding: 3% 7%;
    background-color: #cbddff99;
    position: relative;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    & .about-activity-right-img-border {
      width: 106%;
      height: 106%;
      position: absolute;
      top: -3%;
      left: -3%;
      border: 3px dashed #0066ff;
      border-radius: 10px;
    }
  }
}

.about-activity-left-text,
.about-activity-right-text {
  width: 70%;
  padding: 3%;
  font-size: 1.4rem;
  line-height: 2;
  color: #11438e;
  background-color: #d3e2ff4d;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;

  & p {
    font-weight: 500;
    & span {
      font-weight: 700;
    }
  }
}

.about-activity-left-text {
  align-items: flex-start;
  left: -5%;
}

.about-activity-right-text {
  align-items: flex-end;
  text-align: end;
  right: -5%;
}

#about-logo {
  font-family: 'Black Han Sans';
  font-size: 1.7rem;
  font-weight: 400;
  color: #0066ff;
}

/* 테크스택 */
.about-tech {
  width: 100%;
  margin: 25vh 0;
}

.about-tech-top {
  width: 100%;
  text-align: center;
  font-size: 1.4rem;
  color: #11438e;

  & h4 {
    font-weight: 500;
    & span {
      font-weight: 700;
    }
  }
}

.about-tech-stack {
  width: 100%;
  display: flex;
  flex-direction: column;

  & h1 {
    align-self: flex-end;
    padding: 0.5rem 5% 0.5rem 15%;
    font-family: 'Inter';
    font-size: 3rem;
    font-weight: 700;
    color: white;
    background-color: #3184ff;
  }
}

.about-tech-stack-list {
  width: 100%;
  padding: 2% 0;
  background-color: #dde6ff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5%;
  position: relative;

  & img {
    width: 200px;
    aspect-ratio: 1/1;
  }

  & .about-tech-stack-rectangle {
    width: 20vw;
    height: 10px;
    background-color: #3184ff;
    position: absolute;
  }

  .rectangle__1 {
    top: 15px;
    left: 60px;
  }

  .rectangle__2 {
    bottom: 15px;
    right: 60px;
  }
}

/* 퀴푸 개발팀, 퀴푸뎁, 퀴푸디브 */
.about-dev {
  max-width: 100%;
  margin: 5vh 20%;
  padding: 3%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ebf0ff;
  border-radius: 20px;

  & p {
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 2.5;
    color: #11438e;
    text-align: center;
    & span {
      font-weight: 700;
    }
  }
}

.about-dev-button {
  margin-top: 8vh;
  cursor: pointer;
  min-width: 30%;
  position: relative;

  & h4 {
    margin: 0;
    padding: 3% 5%;
    font-family: Inter;
    font-size: 2rem;
    font-weight: 700;
    color: white;
    text-align: center;
    background: #0066ffcc;
    box-shadow: -5px -5px 4px 0px #00000040 inset;
    border-radius: 10px;
    transition: all 0.5s;

    &:hover {
      box-shadow: 5px 5px 4px 0px #00000040 inset;
    }
  }

  & img {
    width: 60px;
    aspect-ratio: 1/1;
    position: absolute;
    right: -7%;
    bottom: -40%;
  }
}

.about-dev-button-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 3px;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
}

.about-dev-button-list p {
  font-weight: 600;
  border-radius: 10px;
  background-color: #cbddff;
  z-index: 1;
  color: #11438e;
  transition: color 0.5s;

  &:hover {
    color: white;
  }

  &:after {
    position: absolute;
    content: '';
    width: 0;
    height: 50%;
    left: 0;
    z-index: -1;
    background-color: #5e88d799;
    border-radius: 10px;
    transition: width 0.5s;
  }

  &:nth-child(1):after {
    top: 0;
  }
  &:nth-child(2):after {
    top: calc(50% + 1px);
  }
  &:nth-child(1):hover:after {
    width: 100%;
  }
  &:nth-child(2):hover:after {
    width: 100%;
  }
}

/* 왜 스크롤 생기냐 */
.whyscroll {
  width: 100%;
  height: 50px;
}
